@font-face {
  font-family: "Arsilon";
  src: url("/fonts/Arsilon.otf");
}
body {
  font-family: "Poppins";
  font-weight: 400;
}

h1, h2, h3, h4, h5 {
  font-family: "Poppins";
  font-weight: 700;
}

html {
  font-size: 100%;
} /* 16px */
h1 {
  font-size: 4.21rem; /* 67.36px */
}

h2 {
  font-size: 3.158rem; /* 50.56px */
}

h3 {
  font-size: 2.369rem; /* 37.92px */
}

h4 {
  font-size: 1.777rem; /* 28.48px */
}

h5 {
  font-size: 1.333rem; /* 21.28px */
}

small {
  font-size: 0.75rem; /* 12px */
}

@media (max-width: 1024px) {
  h1 {
    font-size: clamp(1.5rem, 7vw, 3rem);
  }
  h2 {
    font-size: clamp(1.5rem, 7vw, 2.4rem);
  }
}
@media (max-width: 640px) {
  h4 {
    font-size: clamp(1.3rem, 4vw, 2.4rem);
  }
  h5 {
    font-size: clamp(1.2rem, 3vw, 2.4rem);
  }
}
header {
  z-index: 5;
  padding: 1em;
}

nav {
  display: flex;
  align-items: center;
}

.logo {
  width: 79px;
  height: 63px;
  background: url("/media/icons/logo.webp") center/cover no-repeat;
  mix-blend-mode: multiply;
}

.nav-links {
  display: flex;
  gap: 1em;
  margin: 0 1.5em 0 auto;
}

.menu {
  display: flex;
  gap: 0.6666666667em;
  position: relative;
}
.menu * {
  cursor: pointer;
}
.menu label {
  position: relative;
}
.menu label::after {
  content: "Menu";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: calc(100% + 0.6666666667em);
}
.menu label div {
  width: 20px;
  height: 2px;
  background: var(--rich-black);
}
.menu label div:nth-child(2) {
  margin: 3px 0;
}
.menu input {
  position: absolute;
  opacity: 0;
}

footer {
  padding: 3em 1em 1em;
  background: color-mix(in srgb, var(--picton-blue), transparent 80%);
}
footer .logo {
  width: 108px;
  height: 84px;
  flex-shrink: 0;
  background: url("/media/icons/logo.webp") center/cover no-repeat;
  mix-blend-mode: multiply;
}
footer .principal {
  display: none;
}
@media (max-width: 475px) {
  footer .principal {
    display: flex;
  }
  footer .minimalist {
    display: none;
  }
}

.principal {
  display: flex;
  align-items: center;
  margin: 0 auto 1.75em;
}

.minimalist {
  width: fit-content;
  margin: 0 auto 1.75em;
}
.minimalist .get-in-touch {
  gap: 1.75em;
}

.get-in-touch {
  display: flex;
  flex-wrap: wrap;
  width: fit-content;
  gap: 0.6666666667em;
}
.get-in-touch a {
  font-weight: 500;
  display: flex;
  gap: 0.6666666667em;
  padding-left: 1em;
}

.links {
  display: grid;
  gap: 2px;
}

.copyright {
  display: flex;
  justify-content: center;
  gap: 4px;
}

/*Desktop First*/
.container {
  max-width: 1536px;
  margin: 0 auto;
}

/*2xl*/
@media (max-width: 1536px) {
  .container {
    max-width: 1280px;
  }
}
@media (min-width: 1536px) {
  .container {
    max-width: 90%;
  }
}
/*xl*/
@media (max-width: 1280px) {
  .container {
    max-width: 1024px;
  }
}
/*lg*/
@media (max-width: 1024px) {
  .container {
    max-width: 768px;
  }
}
/*md*/
@media (max-width: 768px) {
  .container {
    max-width: 640px;
  }
}
/*sm*/
@media (max-width: 640px) {
  .container {
    max-width: 475px;
  }
}
/*xs*/
@media (max-width: 475px) {
  .container {
    width: 100%;
  }
}
#hero {
  height: 80vh;
  display: grid;
  align-items: center;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1)), url("/media/home/001.webp") 50% center/cover no-repeat;
  margin: 1em 1em 3em;
  border-radius: 1em;
  position: relative;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
}
@media (max-width: 1024px) {
  #hero {
    height: clamp(300px, 40vh, 1000px);
  }
}
#hero h1 {
  text-align: center;
  color: var(--white-smoke);
  text-shadow: #000 0px 5px 10px, #000 0px 5px 10px;
  line-height: 1.2;
}
#hero h2 {
  word-spacing: 15px;
  text-align: center;
  font-family: "Arsilon";
  color: var(--white-smoke);
  text-shadow: #000 0px 5px 10px, #000 0px 5px 10px;
}

.c-t-a {
  display: flex;
  justify-content: center;
  gap: 1em;
  margin: 0.6666666667em 0 0;
}
.c-t-a a {
  background: var(--azul);
  padding: 0.6666666667em 1em;
  border-radius: calc(0.6666666667em - 4px);
  font-weight: 600;
  color: var(--white-smoke);
  box-shadow: var(--rich-black) 0px 0px 15px, var(--rich-black) 0px 0px 25px;
}
@media (max-width: 768px) {
  .c-t-a a {
    padding: 4px 8px;
  }
}

#founder {
  min-height: 40vh;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
@media (max-width: 475px) {
  #founder {
    padding: 0 1.5em;
  }
}
@media (max-width: 1024px) {
  #founder {
    flex-direction: column;
  }
  #founder .image {
    position: initial;
    order: 1;
    clip-path: initial;
    width: 100%;
    height: 250px;
    margin: 0 0 1.75em;
    border-radius: 1em;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
    background: url("/media/home/hero.webp") top center/cover no-repeat;
  }
  #founder .image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: top;
  }
  #founder .text {
    order: 2;
  }
}
#founder .image {
  clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
  background: url("/media/home/hero.webp") 0% 50%/cover no-repeat;
  height: 55vh;
  width: 500px;
}
#founder h4 {
  line-height: 1.2;
}
#founder h5 {
  margin: 0 0 0.6666666667em;
}
#founder .verse {
  font-weight: 500;
  position: relative;
}
#founder .verse::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 50%;
  width: 100%;
  background: color-mix(in srgb, var(--green-apple), transparent 30%);
  z-index: -1;
}
#founder p:nth-child(5) {
  font-weight: 600;
}
#founder p:not(:nth-child(5)) {
  margin: 0 0 0.6666666667em;
}
#founder a {
  background: var(--picton-blue);
  padding: 0.6666666667em 1em;
}
#founder .text {
  max-width: 65ch;
}

#goal {
  margin-top: 6em;
  margin-bottom: 6em;
  height: max-content;
}
#goal .content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2em;
}
@media (max-width: 1024px) {
  #goal .content {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 475px) {
  #goal .content {
    padding: 0 1.5em;
    gap: 4em;
  }
}
#goal .content > div {
  border-radius: 1em;
  padding: 1em;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
  position: relative;
  background: white;
}
#goal .content > div h4 {
  position: relative;
  z-index: 1;
}
#goal .content > div h4::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 50%;
  width: 100%;
  background: color-mix(in srgb, var(--canary), transparent 20%);
  z-index: -1;
}
#goal .content > div::before {
  content: "";
  position: absolute;
  bottom: -0.6666666667em;
  right: -0.6666666667em;
  height: 50px;
  width: 70px;
  border-radius: 4px 4px 1.5em 4px;
  background: var(--green-apple);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
  z-index: -1;
}
#goal .content > div::after {
  content: "";
  position: absolute;
  top: -0.6666666667em;
  left: -0.6666666667em;
  height: 50px;
  width: 70px;
  border-radius: 1.5em 4px 4px 4px;
  background: var(--canary);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
  z-index: -1;
}

#process {
  display: grid;
  grid-template-columns: repeat(auto-fit, 45%);
  justify-content: space-evenly;
  gap: 1.75em;
  margin-bottom: 6em;
}
@media (max-width: 640px) {
  #process {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 475px) {
  #process {
    padding: 0 1.5em;
  }
}
#process .images {
  position: relative;
  height: max-content;
}
#process button {
  position: absolute;
  opacity: 0;
}
#process .wrap {
  height: 250px;
  position: relative;
  border-radius: 1em;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
}
#process .wrap::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: calc(100% + 1.5em);
  height: calc(100% + 1.5em);
  border-radius: 1.5em;
  z-index: -1;
  background: var(--green-apple);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
}
#process .pagination {
  display: flex;
  position: absolute;
  bottom: 0.6666666667em;
  left: 50%;
  transform: translateX(-50%);
  gap: 0.6666666667em;
  z-index: 9;
  opacity: 0;
}
#process .pagination span {
  height: 10px;
  aspect-ratio: 1/1;
  border-radius: 50%;
  background: var(--canary);
}
#process h5 {
  width: fit-content;
}
#process p {
  margin: 0.6666666667em 0 0;
  max-width: 50ch;
}

.wrap.slide-1 {
  background: url("/media/home/finality/001.webp") center/cover no-repeat;
}

#way {
  margin-bottom: 6em;
}
@media (max-width: 475px) {
  #way {
    padding: 0 1.5em;
  }
}
#way .content {
  display: flex;
  gap: 2em;
  border-radius: 1em;
  padding: 1em;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
  position: relative;
  background: white;
}
@media (max-width: 1024px) {
  #way .content {
    flex-direction: column;
    gap: 0.6666666667em;
  }
  #way .content .separation {
    height: 3px;
    width: 100%;
    background: color-mix(in srgb, var(--rich-black), transparent 80%);
  }
}
#way .content::before {
  content: "";
  position: absolute;
  bottom: -0.6666666667em;
  right: -0.6666666667em;
  height: 50px;
  width: 70px;
  border-radius: 4px 4px 1.5em 4px;
  background: var(--green-apple);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
  z-index: -2;
}
#way .content::after {
  content: "";
  position: absolute;
  top: -0.6666666667em;
  left: -0.6666666667em;
  height: 50px;
  width: 70px;
  border-radius: 1.5em 4px 4px 4px;
  background: var(--canary);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
  z-index: -2;
}
#way .content .titles {
  display: grid;
  gap: 0.6666666667em;
}
#way .content .titles h5 {
  color: var(--rich-black);
  width: fit-content;
  position: relative;
  z-index: 1;
}
#way .content .titles h5::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 50%;
  width: 100%;
  background: color-mix(in srgb, var(--canary), transparent 20%);
  z-index: -1;
}
#way .content .titles p {
  font-weight: 700;
  color: var(--azul);
}
#way .content .separation {
  width: 3px;
  background: color-mix(in srgb, var(--rich-black), transparent 80%);
}
#way .content p {
  max-width: 70ch;
  position: relative;
  align-self: center;
}

#c-t-a {
  text-align: center;
  padding: 4.5em 0;
  border-top: 2px solid color-mix(in srgb, var(--rich-black), transparent 80%);
  border-bottom: 2px solid color-mix(in srgb, var(--rich-black), transparent 80%);
  margin-top: 6em;
  margin-bottom: 6em;
  background: url("/media/icons/Sprinkle.svg");
}
@media (max-width: 475px) {
  #c-t-a {
    padding: 2em 0 2.5em;
  }
  #c-t-a h3 {
    font-size: 1.75rem;
    max-width: 15ch;
    line-height: 1.2;
    margin: 0 auto 1em;
  }
}
#c-t-a h3 {
  margin: 0 0 0.6666666667em;
}
#c-t-a a {
  padding: 0.6666666667em 2em;
  background: var(--azul);
  font-weight: 600;
  text-transform: uppercase;
  color: white;
  border-radius: 0.6666666667em;
}

#strathero {
  background: url("/media/strategy/001.webp") center/cover no-repeat;
  height: 400px;
  display: grid;
  place-content: center;
  border-radius: 1em;
  margin: 1em 1em 3em;
  border-radius: 1em;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
}
#strathero h2 {
  color: var(--white-smoke);
  text-shadow: #000 0px 5px 10px, #000 0px 5px 10px;
}

#impact {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: space-evenly;
  gap: 3em;
  margin-top: 6em;
  margin-bottom: 6em;
}
#impact > div {
  border-radius: 1em;
  padding: 1em;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
  position: relative;
  background: white;
}
#impact > div h4 {
  position: relative;
  z-index: 1;
}
#impact > div h4::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 50%;
  width: 100%;
  background: color-mix(in srgb, var(--canary), transparent 20%);
  z-index: -1;
}
#impact > div::before {
  content: "";
  position: absolute;
  bottom: -0.6666666667em;
  right: -0.6666666667em;
  height: 50px;
  width: 70px;
  border-radius: 4px 4px 1.5em 4px;
  background: var(--green-apple);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
  z-index: -1;
}
#impact > div::after {
  content: "";
  position: absolute;
  top: -0.6666666667em;
  left: -0.6666666667em;
  height: 50px;
  width: 70px;
  border-radius: 1.5em 4px 4px 4px;
  background: var(--canary);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
  z-index: -1;
}

#effect {
  display: flex;
  justify-content: center;
  margin-bottom: 6em;
  gap: 3em;
}
#effect .image {
  height: 200px;
  aspect-ratio: 2/1;
  background: url("/media/strategy/003.webp") center/cover no-repeat;
  border-radius: 1em;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
}
#effect .text h4 {
  margin-bottom: 0.6666666667em;
}
#effect .text div {
  padding: 0 0 0 1em;
  position: relative;
}
#effect .text div::before {
  content: "";
  position: absolute;
  top: 0;
  left: 2px;
  width: 3px;
  height: 100%;
  background: color-mix(in srgb, var(--rich-black), transparent 70%);
}
#effect .text div:first-of-type {
  margin-bottom: 1em;
}
#effect .text div p {
  max-width: 70ch;
}

#orientation {
  margin-bottom: 6em;
}
#orientation h4 {
  margin: 0 auto 1em;
}
#orientation .list {
  display: flex;
  justify-content: space-evenly;
}
#orientation .list > div {
  width: 300px;
  background: rgb(255, 255, 255);
  padding: 1em;
  border-radius: 1em;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
  position: relative;
}
#orientation .list > div h3 {
  transform: scale(2);
  width: fit-content;
  position: absolute;
  bottom: 8px;
  right: 1.5em;
  opacity: 0.07;
  z-index: 1;
}
#orientation .list > div:before {
  content: "";
  position: absolute;
  bottom: -18px;
  left: 1em;
  height: 24px;
  aspect-ratio: 4/1;
  border-radius: 0.6666666667em;
  background: var(--green-apple);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
  z-index: -1;
}
#orientation .list > div:after {
  content: "";
  position: absolute;
  top: -12px;
  right: 1em;
  height: 24px;
  aspect-ratio: 4/1;
  border-radius: 0.6666666667em;
  background: color-mix(in srgb, var(--canary), white 20%);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
}

#action {
  display: flex;
  justify-content: space-evenly;
  gap: 3em;
  margin-bottom: 6em;
}
#action .sites {
  width: 300px;
  height: auto;
}
#action .sites h5 {
  width: fit-content;
  margin: 0 auto;
  color: var(--rich-black);
  position: relative;
  margin-bottom: 1em;
}
#action .sites h5::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 50%;
  width: 100%;
  background: color-mix(in srgb, var(--canary), transparent 20%);
  z-index: -1;
}
#action .sites img {
  width: 100%;
  height: 100%;
}
#action .wrapper {
  max-width: 75ch;
}
#action .wrapper .slide {
  padding: 0 1em;
}
#action .wrapper .slide div {
  margin: 1em 0;
}
#action .title {
  display: flex;
  justify-content: space-between;
}
#action .title h5:last-child {
  color: color-mix(in srgb, var(--rich-black), transparent 70%);
}
#action .navigation  {
  width: fit-content;
  display: flex;
  gap: 1em;
}
#action button {
  border: none;
  width: 12px;
  aspect-ratio: 1/1;
  transform: rotate(45deg);
}
#action button.prev {
  border-left: 3px solid red;
  border-bottom: 3px solid red;
}
#action button.next {
  border-left: 3px solid red;
  border-top: 3px solid red;
  transform: rotate(135deg);
}

.slide p {
  position: relative;
}
.slide p::before {
  content: "";
  position: absolute;
  left: -0.6666666667em;
  top: 50%;
  height: 90%;
  transform: translateY(-50%);
  width: 3px;
  background: var(--rich-black);
  opacity: 0.3;
}

#title {
  margin-top: 1em;
  height: 200px;
  text-align: center;
  display: grid;
  place-content: center;
  background: url("/media/icons/Sprinkle.svg");
}
@media (max-width: 475px) {
  #title {
    height: 175px;
  }
}

#gallery {
  margin-bottom: 6em;
  min-height: 40vh;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 250px;
  grid-row: 200px;
  gap: 1.5em;
  justify-content: center;
