#gallery .see-more {
  cursor: pointer;
  border: none;
  border-radius: 1em;
  padding: 1em;
  position: relative;
  background: white url("/media/icons/Sprinkle.svg");
}
#gallery .see-more h4 {
  position: relative;
  z-index: 1;
}
#gallery .see-more h4::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 50%;
  width: 100%;
  background: color-mix(in srgb, var(--canary), transparent 20%);
  z-index: -1;
}
#gallery .see-more::before {
  content: "";
  position: absolute;
  bottom: -0.6666666667em;
  right: -0.6666666667em;
  height: 50px;
  width: 70px;
  border-radius: 4px 4px 1.5em 4px;
  background: var(--green-apple);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
  z-index: -1;
}
#gallery .see-more::after {
  content: "";
  position: absolute;
  top: -0.6666666667em;
  left: -0.6666666667em;
  height: 50px;
  width: 70px;
  border-radius: 1.5em 4px 4px 4px;
  background: var(--canary);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1215686275), 0 5px 15px rgba(0, 0, 0, 0.137254902);
  z-index: -1;
}
@media (max-width: 1024px) {
  #gallery {
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: 250px;
  }
}
@media (max-width: 640px) {
  #gallery {
    grid-template-columns: 1fr;
    grid-auto-rows: 250px;
  }
}
@media (max-width: 475px) {
  #gallery {
    grid-auto-rows: 225px;
    padding: 0 1em;
  }
}

